.lists {
    list-style-type: none;
    display: flex;
    justify-content: space-between;
    &_upper {
        position: relative;
    }
}
.paragraph-list{
    &__lists {
        position: absolute;
        top: 100%;
        left: 14px;
    }
    &__text {
        padding: 0 14px;
        font-family: 'Russo One', sans-serif;
        font-weight: 400;
        font-size: 16px;
        line-height: 26px;
        cursor: pointer;
        display: inline-flex;
        align-items: center;
        color: #000000;
       
    }
    &__text_arrow {
        position: relative;
        padding-right: 30px;
        &::before {
            content: '';
            display: block;
            width: 8px;
            height: 8px;
            border: 2px solid #000000;
            border-right-color: transparent;
            border-top-color: transparent;
            border-radius: 3px 3px 3px 0;
            position: absolute;
            right: 10px;
            top: 4px;
            transform: rotate(-45deg);
            transition: 0.3s;
        }
    }      
    &__text_active {
        border-radius: 15px;
        background-color: #e2422d;
        color: #ffdd4d;
        transition: width 0.3s ease-in-out;
        &::before {
            top: 11px;
            border-left-color: #ffdd4d;
            border-bottom-color: #ffdd4d;
            transform: rotate(135deg);
        }
    } 
    &__link {
        transition: 0.3s;
        position: relative;
        color: #000000;
        line-height: 36px;
        font-size: 14px;
        font-family: 'Roboto', sans-serif;
        margin-right: 15px;
        display: inline-block;
        text-decoration: none;
        &::before {
            content: '';
            transition: 0.3s;
            position: absolute;
            display: block;
            width: 0;
            height: 2px;
            top: 80%;
            left: 0;
            background-color: #e2422d;
        }
        &:hover {
            color: #e2422d;
            &::before {
                width: 100%;
            }
        }
    }
}