
body, html {
	margin: 0;
	padding: 0;
	background: #e2422d;
}
ul {
	padding: 0;
	margin: 0;
}
.container {
	position: relative;
	width: 1150px;
	margin: 0 auto;
}
.clearfix::after {
	content: "";
	display: table;
	clear: both;
}
.hidden {
	display: none !important;
}
@import 'header';
@import 'statistics-list';
@import 'lists';
@import 'logo';
@import 'main';