.statistics-list {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #e2422d;
    font-size: 14px;
    width: 100%;
    list-style-type: none;
    &__column {
        width: 555px;
        display: flex;
        justify-content: space-between;
    }
    &__column-container {
        width: 555px;
        display: flex;
        justify-content: space-between;
        list-style-type: none;
    }
    &__paragraph {
        line-height: 36px;
        font-family: 'Roboto', sans-serif;
    }
    &__value {
        background-color: #e2422d;
        padding: 0 8px;
        font-family: 'Russo One', sans-serif;
        font-weight: 400;
        border-radius: 20px;
        color: #ffdd4d;
        font-size: 13px;
    }
}
