.header {
    display: flex;
    justify-content: center;
    &__wraper {
        height: 230px;
        width: 1600px;
        min-width: 1600px;
        background-color: #ff8100;
        display: flex;
        justify-content: center;
        align-items: flex-end;
        background-image: linear-gradient(180deg, #fffa00, #ff8100);
        border-radius: 0 0 50% 50%/0 0 100% 100%;
        margin-top: -70px;
        box-shadow: 0 24px 64px 0 rgba(0, 0, 0, 0.05);
    }
    &__container {
        width: 930px;
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
    }
    &__container_nav {
        justify-content: space-between;
    }
    &__nav {
        width: 720px;
        margin-top: 20px;
        margin-bottom: 75px;
    }
}